import {CONSTANTS} from './constants';
import {ACTIONS} from './constants';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {getPopoverUserObject} from './selectors';
import "@servicenow/now-avatar";

function getPopupContainer() {
	return document.getElementById(CONSTANTS.POPOVER_CONTAINER) || createPopupContainer();
}

function createPopupContainer() {
	const containerDiv = document.createElement('div');
	containerDiv.setAttribute('id', CONSTANTS.POPOVER_CONTAINER);
	containerDiv.attachShadow({mode: 'open'});
	document.body.appendChild(containerDiv);
	return containerDiv;
}

function isWhiteListed(el) {
	return el.classList.contains(CONSTANTS.WHITELISTED_CLASSES);
}

function getStyleComputedProperty(element, property) {
	if (element.nodeType !== 1)
		return [];

	const window = element.ownerDocument.defaultView;
	const css = window.getComputedStyle(element, null);
	return property ? css[property] : css;
}

function getParentNode(element) {
	return element.parentNode || element.host;
}

/**
 * Get the container dimension of the parent element of the user card.
 * @param {HTMLElement} element
 */
function getContainerElementDimension(element) {
	if (element.nodeName) {
		const {height, top} = element.getBoundingClientRect();
		return {
			height,
			top,
		};
	}
	// Else if the element is a window object
	else if (element.self === window) {
		return {
			height: element.innerHeight,
			top: 0,
		};
	}
}

/**
 * Gets the scrolling parent of the user card. Will default to the window element if none
 * is found.
 * @param {HTMLElement} element
 */
export function getScrollingParent(element) {
	if (!element)
		return window;

	if (element === '#document')
		return window;

	const { overflow, overflowX, overflowY } = getStyleComputedProperty(element);

	if (/(auto|scroll|overlay)/.test(overflow + overflowY + overflowX)) {
		if (isWhiteListed(element)) {
			return element;
		}
	}

	return getScrollingParent(getParentNode(element));
}

export function getCardSize(size) {
	return CONSTANTS.SIZE_CLASS[size.toUpperCase()];
}

/**
 *	Remove the popover from the container.
 */
export const removePopover = () => {
	const {shadowRoot} = getPopupContainer();
	snabbdom.onStateChange(shadowRoot, () => undefined);
};

/**
 * Render the user card popover in the popover container.
 * @param {seismicState} state
 * @param {seismicDispatch} dispatch
 */
export const patchPopover = (state, dispatch) => {
	const {size, userData, userCardRef, linkRef} = state;
	const {popoverConfig} = state.properties;
	const {shadowRoot} = getPopupContainer();
	let scrollingParent = getScrollingParent(userCardRef);

	const closePopover = () => {
		dispatch(ACTIONS.TOGGLE_POPOVER, {isPopoverActive: false});
		removePopover();
		linkRef.focus();
	};

	snabbdom.onStateChange(shadowRoot, () => {
		return (
			<sn-component-user-popover-card
				componentId="userPopoverCard"
				size={size}
				user={getPopoverUserObject(userData, popoverConfig)}
				parentLocation={userCardRef.getBoundingClientRect()}
				scrollParentLocation={getContainerElementDimension(scrollingParent)}
				closePopover={closePopover}
			>
			</sn-component-user-popover-card>
		);
	});
};

export const renderAvatar = (userData, size) => {
	return (
		<now-avatar
			size={size}
			user-name={userData.name}
			image-src={userData.photo || ''}
		/>
	);
};
