import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {createEnhancedElement} from "@servicenow/library-enhanced-element";
import isEmpty from 'lodash/isEmpty';
import "@servicenow/now-avatar";

import {ACTIONS, CONSTANTS} from './constants';
import {
	loadUserEffect,
	addActionPayloadToStateEffect,
	updatePopoverLocationEffect,
	fetchUserDataEffect,
	fetchUserDataFromPropsEffect,
	loadUserStartEffect,
	loadUserSuccessEffect,
	loadUserErrorEffect,
	togglePopoverEffect,
} from './effects';
import {getCardSize} from './util';
import eventListenerBehavior from './behaviors';
import CardBody from './views';
import style from './userCard.scss';
import '../userPopoverCard';

/**
 * @seismicView sn-component-user-card
 */
const userCard = (state, {dispatch}) => {
	const {userData, properties: {size}} = state;
	const isDisabled = (userData) => { return isEmpty(userData) ? CONSTANTS.USER_CARD_DISABLED_CLASS : ''};

	return (
		<div
			className={`now-user-card ${getCardSize(size)} ${isDisabled(userData)}`}
			hook-insert={({elm}) => dispatch(ACTIONS.REDUCE_STATE, {userCardRef: elm})}
			on-click={() => {
				handleClick(state, dispatch);
			}}
			onkeydown={(keyboardEvent) => {
				if (keyboardEvent.keyCode === CONSTANTS.KEY_CODE_ENTER || keyboardEvent.keyCode === CONSTANTS.KEY_CODE_SPACE)
					handleClick(state, dispatch);
			}}
			role="button"
		>
			{CardBody(state, dispatch)}
		</div>
	);
};

const handleClick = (state, dispatch) => {
	const {isPopoverActive, isLoading, userData, loadingError} = state;
	if (!(isLoading || loadingError || isEmpty(userData)))
		dispatch(ACTIONS.TOGGLE_POPOVER, {
			isPopoverActive: !isPopoverActive,
			shouldRemovePopover: true,
		});
};

/**
* Element that displays the user's avatar and information. If clicked, it will show more
* detailed information as a popover.
* ```
* <sn-component-user-card
*     size="lg"
*     sysId="6816f79cc0a8016401c5a33be04be441"
*	  popoverConfig={config}
*     title="Software Engineer"
* >
* </sn-component-user-card>
* ```
* @seismicElement sn-component-user-card
*/
createEnhancedElement('sn-component-user-card', {
	initialState: {
		/**
		 * Internal user data state that is set either by the result of loadUserEffect, or the user data if passed in.
		 * This will be passed into the user popover card.
		 * @type {{email:string, initials: string, name: string, phone: string, photo: string, title: string}}
		 */
		userData: {},
		/**
		 * Reference of the user card component.
		 * @type {VirtualNode}
		 */
		userCardRef: null,
		/**
		 * Track whether the popover is active for this component.
		 * @type {boolean}
		 */
		isPopoverActive: false,
		/**
		 * Current loading status of the component.
		 * @type {boolean}
		 */
		isLoading: false,
		/**
		 * Indicate if there was an  error loading the component.
		 * @type {boolean}
		 */
		loadingError: false,
	},

	locals: {
		/**
		 * Local functions used to handle the position of the user card popover in relation to this component.
		 * @type {{click: Function, resize: Function, scroll: Function}}
		 */
		eventListeners: {
			default: {
				click: null,
				resize: null,
				scroll: null
			}
		}
	},

	properties: {
		/**
		 * An object that determines what information to be display on the user popover card.
		 * @type {Object}
		 */
		popoverConfig: {
			default: {
				email: true,
				initials: true,
				name: true,
				phone: true,
				photo: true,
				title: true,
			}
		},
		/**
		 * Size of the user card.
		 * @type {('sm' | 'md' | 'lg')}
		 */
		size: {default: 'md'},
		/**
		 * sys_id of the user to fetch information for.
		 * @type {string}
		 */
		sysId: {
			onChange: (value, previousValue, dispatch) => {
				dispatch(ACTIONS.FETCH_USER_DATA, {sysId: value});
			}
		},
		/**
		 * Title to display on the user card.
		 * @type {string}
		 */
		title: {reflect: false},

		/**
		 * An object that contains information about a user.
		 * @type {{email:string, firstName: string, initials: string, lastName: string, phone: string, photo: string, title: string}}
		 */
		user: {
			onChange: (value, previousValue, dispatch) => {
				dispatch(ACTIONS.FETCH_USER_DATA_FROM_PROP, {user: value});
			}
		},
		/**
		 * Property to indicate whether an element is assigned or not. A user card is unassigned if the sys_id
		 * does not return a user or if the user object passed in is empty.
		 */
		unassigned: {
			default: true,
			readOnly: true,
			reflect: true,
		}
	},

	onConnect: (host, {dispatch}) => {
		const {sysId} = host;
		dispatch(ACTIONS.FETCH_USER_DATA, {sysId});
	},

	actionHandlers: {
		[ACTIONS.LOAD_USER]: loadUserEffect,
		[ACTIONS.LOAD_USER_START]: loadUserStartEffect,
		[ACTIONS.LOAD_USER_SUCCESS]: loadUserSuccessEffect,
		[ACTIONS.LOAD_USER_ERROR]: loadUserErrorEffect,

		[ACTIONS.REDUCE_STATE]: addActionPayloadToStateEffect,
		[ACTIONS.TOGGLE_POPOVER]: togglePopoverEffect,
		[ACTIONS.UPDATE_POPOVER_LOCATION]: updatePopoverLocationEffect,
		[ACTIONS.FETCH_USER_DATA]: fetchUserDataEffect,
		[ACTIONS.FETCH_USER_DATA_FROM_PROP]: fetchUserDataFromPropsEffect,
	},

	behaviors: [{
		behavior: eventListenerBehavior
	}],

	renderer: {
		view: userCard,
		type: snabbdom,
	},

	styles: style
});
