import {renderAvatar} from '../../userCard/util';

const renderName = (name) => {
	if (name)
		return (<div className="sn-card--title"><span>{name}</span></div>);
};

const renderTitle = (title) => {
	if (title)
		return (<div className="sn-card--byline"><span>{title}</span></div>);
};

const renderUserDetails = (user) => {
	if (!user.name && !user.title)
		return;
	return (
		<div className="sn-card--content">
			{renderName(user.name)}
			{renderTitle(user.title)}
		</div>
	);
};

export default (user, size) => {
	return (
        <div className="sn-card--row">
			<div className="sn-card--content sn-card--content__static">
				<div className="sn-avatar-container">
					{renderAvatar(user, size)}
				</div>
			</div>
			{renderUserDetails(user)}
		</div>
    );
};
