import {createEnhancedElement} from "@servicenow/library-enhanced-element";
import snabbdom, {createRef} from '@servicenow/ui-renderer-snabbdom';
import {t} from 'sn-translate';
import {POS, POPOVER_SPACING, ESCAPE_KEY, TAB_KEY} from './constants';
import {cardContent} from './views';
import style from './userPopoverCard.scss';

/**
 * Handle keyboard events on the user card
 * @param {KeyboardEvent}
 */
const handleKeyDown = closePopover => event => {
	switch (event.keyCode) {
	case ESCAPE_KEY:
		closePopover();
		break;
	case TAB_KEY:
		event.preventDefault();
		break;
	}
};

/**
 * Determine the orientation of the popover.
 * @param {DOMRect} scrollParentRect
 * @param {DOMRect} parentLocation
 * @param {DOMRect} popoverLocation
 *
 * @return {('TOP' | 'BOTTOM')}
 */
const determineOrientation = (scrollParentRect, parentLocation, popoverLocation) => {
	const topBoundary = parentLocation.top - popoverLocation.height - POPOVER_SPACING + window.pageYOffset;
	return (topBoundary <= scrollParentRect.top) ? POS.BOTTOM : POS.TOP;
};

/**
 * Calculate where to place the popover in the view.
 * @param {DOMRect} parentLocation
 * @param {DOMRect} scrollParentLocation
 * @param {DOMRect} popoverLocation
 *
 * @return {pos:('TOP' | 'BOTTOM'), style:{top:string, left:string}}
 */
const getPopoverPosition = (parentLocation, scrollParentLocation, popoverLocation) => {
	let style = {};
	if (!popoverLocation || !scrollParentLocation)
		return null;

	const orientation = determineOrientation(scrollParentLocation, parentLocation, popoverLocation);

	// Note: Currently, right and left cases are not being used, but left code here
	// in case it is used in the future.
	switch (orientation) {
	case POS.TOP:
		style.top = parentLocation.top - popoverLocation.height - POPOVER_SPACING + window.pageYOffset;
		style.left = parentLocation.left + (parentLocation.width / 2) - (popoverLocation.width / 2) + window.pageXOffset;
		break;
	case POS.BOTTOM:
		style.top = parentLocation.top + parentLocation.height + POPOVER_SPACING + window.pageYOffset;
		style.left = parentLocation.left + (parentLocation.width / 2) - (popoverLocation.width / 2) + window.pageXOffset;
		break;
	/* Leaving here in case that one day we decide to support horizontal repositioning
	case POS.RIGHT:
		style.top = parentLocation.top + (parentLocation.height / 2) - (popoverLocation.height / 2) + window.pageYOffset;
		style.left = parentLocation.right + POPOVER_SPACING + window.pageXOffset;
		break;
	case POS.LEFT:
		style.top = parentLocation.top + (parentLocation.height / 2) - (popoverLocation.height / 2) + window.pageYOffset;
		style.left = parentLocation.left - popoverLocation.width - POPOVER_SPACING - window.pageXOffset;
		break;
	*/
	default:
		break;
	}

	// Check if left position would be outside of window's left boundary
	if (style.left < 0)
		style.left = POPOVER_SPACING;

	const aboveScrollParent = style.top < scrollParentLocation.top;
	const overflowScrollParent = (style.top + popoverLocation.height) > (Math.abs(scrollParentLocation.top) + window.pageYOffset + scrollParentLocation.height);

	// Check if we are going to position the popover past the top or bottom boundaries of our scrolling parent...
	if (aboveScrollParent || overflowScrollParent) {
		style.top = -popoverLocation.height;
		style.left = -popoverLocation.width;
	}

	style.top = `${style.top}px`;
	style.left = `${style.left}px`;

	return {style, pos: orientation};
};

/**
 * Set position of the user popover in relation to the defined parent and scrolling parent.
 * @param {{parentLocation: DOMRect, scrollParentLocation: DOMRect, element: DOMRect}} param0
 */
const setPopoverPosition = ({parentLocation, scrollParentLocation, element}) => {
	if (!parentLocation || !scrollParentLocation || !element)
		return null;

	let position = getPopoverPosition(parentLocation, scrollParentLocation, element.getBoundingClientRect());

	if (!position) {
		position = {
			style: {},
			pos: POS.TOP
		};
	}

	if (position.pos === POS.BOTTOM)
		element.classList.add('sn-persona-shadow--top');

	element.style.top = position.style.top;
	element.style.left = position.style.left;

	element.focus();
};

/**
 * @seismicView sn-component-user-card-popover
 */
const userCardPopover = (state) => {
	const {user, size, parentLocation, scrollParentLocation, closePopover} = state.properties;
	let popoverRef = createRef();
	return (
		<div
			aria-describedby="popover-body-0"
			aria-label={`${t('Popover Menu Title')}`}
			aria-modal="true"
			className="sn-popover has-card"
			hook-insert={() => {
				setPopoverPosition({
					parentLocation,
					scrollParentLocation,
					element: popoverRef.current
				});
			}}
			hook-update={() => {
				setPopoverPosition({
					parentLocation,
					scrollParentLocation,
					element: popoverRef.current
				});
			}}
			on-keydown={handleKeyDown(closePopover)}
			ref={popoverRef}
			role="dialog"
			tabIndex={-1}
		>
			<div className="sn-popover--body" id="popover-body-0">
				{cardContent(user, size)}
			</div>
		</div>
	);
};

/**
 * Element that displays information as a popover
 * ```
 * <sn-component-user-popover-card
 *     size={size}
 *     user={user}
 *     parentLocation={parentLocation}
 *     scrollParentLocation={scrollParentLocation}
 * >
 * </sn-component-user-card-popover>
 * ```
 * @seismicElement sn-component-user-popover-card
 */
createEnhancedElement('sn-component-user-popover-card', {
	initialState: {},

	properties: {
		/**
		 * An object that contains information about a user.
		 * @type {{email:string, initials: string, name: string, phone: string, photo: string, title: string}}
		 */
		user: {},
		/**
		 * Size of the user card
		 * @type {('sm' | 'md' | 'lg')}
		 */
		size: {default: 'md'},
		/**
		 * Custom function to remove the popover from the view
		 * @type {Function}
		 */
		closePopover: {},
		/**
		 * The DOM position of the component that is calling this component
		 * @type {DOMRect}
		 */
		parentLocation: {},
		/**
		 * The position of the scrolling element of the page this component is on
		 *@type {{height: number, top: number}}
		 */
		scrollParentLocation: {},
	},

	renderer: {view: userCardPopover, type: snabbdom},

	styles: style
});