export const userQuery = `
	query($sys_id: String) {
		GlideRecord_Query {
			__typename
			sys_user(sys_id: $sys_id) {
				_table_metadata {
					canRead
				}
				_results {
					first_name {
						value
						displayValue
					}
					last_name {
						value
						displayValue
					}
					title{
						value
						displayValue
					}
					email {
						value
						displayValue
					}
					phone {
						value
						displayValue
					}
					photo {
						value
						displayValue
					}
					_query{
						user_photo: GlideDomain_Query {
						userInfo(sys_id: "$$parent.sys_id$$") {
							avatar
							}
						}
					}
				}
			}
		}
	}
	`;