import {get, isEmpty} from 'lodash';

export function getInitials(user) {
	if (!user || !user.first_name)
		return null;
	let initials = `${user.first_name.displayValue.charAt(0)}`;

	if (user.last_name)
		initials = `${initials}${user.last_name.displayValue.charAt(0)}`;

	return initials.toUpperCase();
}

export function getInitialsFromProps(props) {
	const {firstName, lastName} = props;
	let initials = '';
	if (firstName)
		initials += firstName.charAt(0);
	if (lastName)
		initials += lastName.charAt(0);
	return initials;
}

export function getPhoto(user) {
	const profile = get(user, '_query.user_photo.userInfo', null);

	if (!profile || !profile.avatar)
		return null;

	return `/${profile.avatar}`;
}

export function getName(user) {
	if (!user || !user.first_name || !user.last_name)
		return null;

	return `${user.first_name.displayValue} ${user.last_name.displayValue}`;
}

export function getEmail(user) {
	if (!user || !user.email || !user.email.value)
		return null;

	return `${user.email.displayValue}`;
}

export function getPhone(user) {
	if (!user || !user.phone || !user.phone.value)
		return null;

	return `${user.phone.displayValue}`;
}

export function getTitle(user) {
	if (!user || !user.title || !user.title.value)
		return null;

	return `${user.title.displayValue}`;
}

export function getPopoverUserObject(user, config) {
	if (!user || !config)
		return null;

	return {
		initials: config.initials ? user.initials : null,
		photo: config.photo ? user.photo : null,
		name: config.name ? user.name : null,
		title: config.title ? user.title : null,
		email: config.email ? user.email : null,
		phone: config.phone ? user.phone : null
	};
}

export function getUserObject(object) {
	const user = get(object, 'GlideRecord_Query.sys_user._results[0]', {});
	if (isEmpty(user))
		return;

	return {
		initials: getInitials(user),
		photo: getPhoto(user),
		name: getName(user),
		title: getTitle(user),
		email: getEmail(user),
		phone: getPhone(user)
	};
}

export function getUserObjectFromProps(user) {
	if (!user)
		return null;

	return {
		initials: getInitialsFromProps(user),
		name: (`${user.firstName || ''} ${user.lastName || ''}`).trim(),
		title: user.title || null,
		email: user.email || null,
		phone: user.phone || null,
		photo: user.photo || null
	};
}