import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import isEmpty from 'lodash/isEmpty';
import {ACTIONS} from './constants';
import {patchPopover} from './util';
import {getUserObject} from './selectors';
import {userQuery} from './query';
import {getUserObjectFromProps} from './selectors';

/**
 * Create a graphql effect to fetch user data.
 */
export const loadUserEffect = createGraphQLEffect(userQuery, {
	variableList: ['sys_id'],
	startActionType: ACTIONS.LOAD_USER_START,
	successActionType: ACTIONS.LOAD_USER_SUCCESS,
	errorActionType: ACTIONS.LOAD_USER_ERROR,
});

/**
 * Fetch data user data from a sys_id
 */
export const fetchUserDataEffect = ({state, action, dispatch}) => {
	const {payload: {sysId}} = action;
	const {properties, properties: {user}} = state;

	if (sysId)
		dispatch(ACTIONS.LOAD_USER, {sys_id: sysId});

	else if (user) {
		// If sysID is not set, than override the view with the user property
		const userData = getUserObjectFromProps(user);

		dispatch(ACTIONS.REDUCE_STATE, {
			userData,
			properties: {...properties, unassigned: isEmpty(userData)}
		});
	}

	else {
		dispatch(ACTIONS.REDUCE_STATE, {
			userData: {},
			properties: {...properties, unassigned: true}
		});
	}
};

/**
 * Fetch user data from the user data property
 */
export const fetchUserDataFromPropsEffect = ({state, action, dispatch}) => {
	const {payload: {user}} = action;
	const {properties, properties: {sysId}} = state;

	// Only set user if sysId is not set
	if (!sysId) {
		// If sysID is not set, than override the view with the user property
		const userData = getUserObjectFromProps(user);

		dispatch(ACTIONS.REDUCE_STATE, {
			userData,
			properties: {...properties, unassigned: isEmpty(userData)}
		});
	}
};

/**
 * Create effect to add start loading state.
 */
export const loadUserStartEffect = ({state, updateState, action}) => {
	updateState({
		isLoading: true
	});
};

/**
 * Create effect to add payload to the state.
 */
export const addActionPayloadToStateEffect = ({updateState, action}) => {
	updateState({
		...action.payload
	});
};

export const togglePopoverEffect = ({action, dispatch}) => {
	const {payload: {isPopoverActive, shouldRemovePopover}} = action;
	dispatch(ACTIONS.REDUCE_STATE, {isPopoverActive});
	if (isPopoverActive)
		dispatch(ACTIONS.ADD_EVENT_LISTENERS, {isPopoverActive});
	else
		dispatch(ACTIONS.REMOVE_EVENT_LISTENERS, {shouldRemovePopover});
};

/**
 * Create effect to handle loading success.
 */
export const loadUserSuccessEffect = ({state, updateState, action}) => {
	const {payload: {data}} = action;
	const {properties} = state;
	const userData = getUserObject(data);
	updateState({
		userData,
		isLoading: false,
		properties: {
			...properties,
			unassigned: isEmpty(userData)
		}
	});
};

/**
 * Create effect to handle loading error.
 */
export const loadUserErrorEffect = ({state, updateState}) => {
	updateState({
		loadingError: true,
		isLoading: false
	});
};

/**
 * Create effect to adjust the position of the popover.
 */
export const updatePopoverLocationEffect = ({state, dispatch}) => {
	patchPopover(state, dispatch);
};