import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import find from 'lodash/find';
import debounce from 'lodash/debounce';

import {ACTIONS} from './constants';
import {removePopover, patchPopover, getScrollingParent} from './util';

const eventListenerBehavior = {
	name: 'eventListenerBehavior',

	actionHandlers: {
		[ACTIONS.ADD_EVENT_LISTENERS]: ({action, state, dispatch}) => {
			const {payload: {isPopoverActive}} = action;
			const {userData, userCardRef} = state;
			const scrollingParent = getScrollingParent(userCardRef);
			/**
			 * Add an event listener to toggle off this component when the HTML body element is clicked
			 * @param {Event} e
			 */
			const handleBodyElementClick = (e) => {
				const foundAssignedUserCard = find(e.composedPath(), (element) => {
					return element.nodeName === 'SN-COMPONENT-USER-CARD' && !element['unassigned'];
				});

				const shouldRemovePopover = !foundAssignedUserCard || e.composedPath().includes(userCardRef);

				// Do not remove the popover from the view if the click event happend within another assigned
				// user card component on the page.
				if (shouldRemovePopover)
					removePopover();

				// Set the popover to active if the click event happened outside of the current user card component
				if (isPopoverActive && !e.composedPath().includes(userCardRef))
					dispatch(ACTIONS.TOGGLE_POPOVER, {isPopoverActive: false, shouldRemovePopover});
			};

			const handlePopoverReposition = () => {
				if (!isEmpty(userData))
					dispatch(ACTIONS.UPDATE_POPOVER_LOCATION);
			};

			const handleResize = debounce(handlePopoverReposition, 25);

			patchPopover(state, dispatch);

			// Wrap this in a setTimeouut to force the listeners to be applied on the next tick of the event loop.
			setTimeout(() => {
				document.body.addEventListener('click', handleBodyElementClick);
				window.addEventListener('resize', handleResize);
				if (scrollingParent.self !== window)
					scrollingParent.addEventListener('scroll', handlePopoverReposition);
			}, 0);

			dispatch(ACTIONS.LOCALS_SET, {
				eventListeners: {
					click: handleBodyElementClick,
					resize: handleResize,
					scroll: handlePopoverReposition
				}
			});
		},

		[ACTIONS.REMOVE_EVENT_LISTENERS]: ({action, state, dispatch}) => {
			const {userCardRef} = state;
			const {payload} = action;
			const {eventListeners} = get(state, 'locals', {});
			const scrollingParent = getScrollingParent(userCardRef);

			if (payload && payload.shouldRemovePopover)
				removePopover();

			if (eventListeners.click)
				document.body.removeEventListener('click', eventListeners.click);

			if (eventListeners.resize)
				window.removeEventListener('resize', eventListeners.resize);

			if (eventListeners.scroll)
				scrollingParent.removeEventListener('scroll', eventListeners.scroll);

			dispatch(ACTIONS.LOCALS_SET, {
				eventListeners: {
					click: null,
					resize: null,
					scroll: null
				}
			});
		},
	},

	onDisconnect(el, dispatch) {
		dispatch(ACTIONS.REMOVE_EVENT_LISTENERS);
	}
};

export default eventListenerBehavior;