import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import isEmpty from 'lodash/isEmpty';
import {t} from 'sn-translate';

import CardMessage from './cardMessage';
import {ACTIONS} from '../constants';
import {renderAvatar} from '../util';

export default (state, dispatch) => {
	const {size, title} = state.properties;
	const {isPopoverActive, userData, isLoading, loadingError} = state;
	const isFocusClass = isPopoverActive ? 'is-focus' : '';

	if (isLoading)
		return CardMessage(title, size, t('Loading user...'));

	if (isEmpty(userData) || loadingError) {
		return CardMessage(title, size, t('Unassigned'));
	}

	return (
        <Fragment>
			<div
				className={isFocusClass}
				hook-insert={vnode => dispatch(ACTIONS.REDUCE_STATE, {linkRef: vnode.elm})}
				tabIndex="0">
				{renderAvatar(userData, size)}
				<div className="now-user-card__name">
					<span>{userData.name}</span>
					<span>{title || userData.title}</span>
				</div>
			</div>
		</Fragment>
    );
};