import "@servicenow/now-text-link";

import {NOT_AVAILABLE, EMAIL_LABEL, PHONE_LABEL} from '../constants';

const renderEmailLink = (email) => {
	if (email) {
		return (
            <span>
				<now-text-link label={email} href={`mailto:${email}`}></now-text-link>
			</span>
        );
	}

	return (
		<span>
			{NOT_AVAILABLE}
		</span>
	);
};

const renderEmail = (email) => {
	return (
		<tr>
			<td className='sn-card-footer-label'>
				<span>{EMAIL_LABEL}</span>
			</td>
			<td>
				{renderEmailLink(email)}
			</td>
		</tr>
	);
};

const renderPhone = (phone) => {
	return (
		<tr>
			<td className='sn-card-footer-label'>
				<span>{PHONE_LABEL}</span>
			</td>
			<td>
				<span>{phone ? phone : NOT_AVAILABLE}</span>
			</td>
		</tr>
	);
};

export default (user) => (
	<div className="sn-card--row">
		<table>
			<tbody>
				{renderEmail(user.email)}
				{renderPhone(user.phone)}
			</tbody>
		</table>
	</div>
);